.not_found {
  display: grid;
  justify-content: center;
  grid-template-areas: 'text text' 'btn1 btn2';
  gap: 40px;

  height: 100%;
  width: 100%;

  &_text {
    grid-area: text;
    text-align: center;
    font-size: 1.2rem;
  }

  &_btn {
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;

    box-sizing: border-box;
    border: 4px solid;
    height: 48px;
    width: 300px;

    background-color: transparent;
    overflow: hidden;
    
    color: currentColor;
    text-decoration: none;
    font-size: 1rem;

    &::after {
      content: '';
      position: absolute;

      background-color: currentColor;
      width: 0;
      height: 100%;

      transition: width 0.3s linear;
    }
  }

  &_btn1 {
    grid-area: btn1;
  }

  &_btn2 {
    grid-area: btn2;
  }
}

@media (hover: hover) and (pointer: fine) {
  .not_found {
    &_btn:hover {
      background-color: black;
    }
  }
}